export interface User {
  id: string;
  email: string;
  name: string;
}

export interface LoginCredentials {
  email: string;
  password: string;
}

export interface AuthResponse {
  success: boolean;
  user?: User;
  message: string;
}

// Mock user database
const mockUsers: User[] = [
  {
    id: '1',
    email: 'test@example.com',
    name: 'Test User'
  },
  {
    id: '2',
    email: 'demo@amazon.com',
    name: 'Demo User'
  },
  {
    id: '3',
    email: 'user@test.com',
    name: 'Sample User'
  }
];

// Mock credentials (in real app, passwords would be hashed)
const mockCredentials = [
  { email: 'test@example.com', password: '123456' },
  { email: 'demo@amazon.com', password: 'password' },
  { email: 'user@test.com', password: 'demo123' }
];

export const login = async (credentials: LoginCredentials): Promise<AuthResponse> => {
  try {
    // Simulate API delay
    await new Promise(resolve => setTimeout(resolve, 1000));

    const { email, password } = credentials;

    // Validate input
    if (!email || !password) {
      return {
        success: false,
        message: 'Email and password are required'
      };
    }

    // Check credentials
    const validCredential = mockCredentials.find(
      cred => cred.email === email && cred.password === password
    );

    if (!validCredential) {
      return {
        success: false,
        message: 'Invalid email or password'
      };
    }

    // Find user
    const user = mockUsers.find(u => u.email === email);

    if (!user) {
      return {
        success: false,
        message: 'User not found'
      };
    }

    // Store user in localStorage (mock session)
    if (typeof window !== 'undefined') {
      localStorage.setItem('currentUser', JSON.stringify(user));
    }

    return {
      success: true,
      user,
      message: 'Login successful'
    };

  } catch (error) {
    console.error('Login error:', error);
    return {
      success: false,
      message: 'An error occurred during login'
    };
  }
};

export const logout = async (): Promise<AuthResponse> => {
  try {
    // Simulate API delay
    await new Promise(resolve => setTimeout(resolve, 500));

    // Remove user from localStorage
    if (typeof window !== 'undefined') {
      localStorage.removeItem('currentUser');
    }

    return {
      success: true,
      message: 'Logout successful'
    };

  } catch (error) {
    console.error('Logout error:', error);
    return {
      success: false,
      message: 'An error occurred during logout'
    };
  }
};

export const getCurrentUser = (): User | null => {
  try {
    if (typeof window === 'undefined') {
      return null;
    }

    const userStr = localStorage.getItem('currentUser');
    if (!userStr) {
      return null;
    }

    return JSON.parse(userStr) as User;

  } catch (error) {
    console.error('Error getting current user:', error);
    return null;
  }
};

export const isAuthenticated = (): boolean => {
  try {
    return getCurrentUser() !== null;
  } catch (error) {
    console.error('Error checking authentication:', error);
    return false;
  }
};

export const register = async (userData: {
  email: string;
  password: string;
  name: string;
}): Promise<AuthResponse> => {
  try {
    // Simulate API delay
    await new Promise(resolve => setTimeout(resolve, 1000));

    const { email, password, name } = userData;

    // Validate input
    if (!email || !password || !name) {
      return {
        success: false,
        message: 'All fields are required'
      };
    }

    // Check if user already exists
    const existingUser = mockUsers.find(u => u.email === email);
    if (existingUser) {
      return {
        success: false,
        message: 'User with this email already exists'
      };
    }

    // Create new user
    const newUser: User = {
      id: (mockUsers.length + 1).toString(),
      email,
      name
    };

    // Add to mock database
    mockUsers.push(newUser);
    mockCredentials.push({ email, password });

    // Store user in localStorage
    if (typeof window !== 'undefined') {
      localStorage.setItem('currentUser', JSON.stringify(newUser));
    }

    return {
      success: true,
      user: newUser,
      message: 'Registration successful'
    };

  } catch (error) {
    console.error('Registration error:', error);
    return {
      success: false,
      message: 'An error occurred during registration'
    };
  }
};
